
.page-search {
}
.search-box {
  position: fixed;
  top: 3.5rem;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: .5rem;
  height: 3rem;
}
.search-inp {
  width: calc(100% - 4rem);
  border: 0;
  border-radius: 5px 0 0 5px;
  text-indent: .6rem;
}
.search-btn {
  width: 3rem;
  border: 0;
  border-radius: 0 5px 5px 0;
  background-color: #0aa;
  color: #fff;
}
.list-box {
  box-sizing: border-box;
  margin-top: 7rem;
  background-color: #fff;
  overflow: scroll;
}
.list-box .list-ul {
  display: block;
  margin: 0;
  overflow-y: visible;
}
.list-box .list-li {
  line-height: 1.2rem;
  white-space: 1px;
  padding: .5rem 0;
  border-bottom: 1px dashed #7CDBF1;
  font-size: 15px;
}
.brand-loading-p {
  vertical-align: middle;
  text-align: center;
}
.page-infinite-loading>span {
  display: inline-block
}
.load-box-center {
  margin: .6rem auto;
  width: 100px;
}
.load-box-center>span {
  float: left;
}
.brand-loading-text {
  display: block;
  float: left;
  height: 28px;
  line-height: 28px;
}
@component-namespace page {
@component infinite {
@descendent desc {
      text-align: center;
      color: #666;
      padding-bottom: 5px;
      border-bottom: solid 1px #eee;
}
@descendent listitem {
      height: 50px;
      line-height: 50px;
      border-bottom: solid 1px #eee;
      text-align: center;
&:first-child {
        border-top: solid 1px #eee;
}
}
@descendent wrapper {
      margin-top: -1px;
      overflow: scroll;
}
@descendent loading {
      text-align: center;
      height: 50px;
      line-height: 50px;
div {
        display: inline-block;
        vertical-align: middle;
        margin-right: 5px;
}
}
}
}











