
@media screen and (max-width:320px){
ul.mint-indexlist-navlist{
     margin-bottom: 480px;
     -webkit-transform: scale(0.89,0.89);
             transform: scale(0.89,0.89);
}
}
.mint-indexlist-nav{
    right:auto;
    left:0;
    border-right:solid 1px #ddd;
}
.mint-indexlist-content{
    margin-right:0!important;
    margin-left:80px;
    text-align: center;
}
@component-namespace page {
@component indexlist {
@descendent desc {
      text-align: center;
      color: #666;
      padding-bottom: 5px;
}
@descendent wrapper {
      width: 100%;
      border-top: solid 1px #ddd;
}
}
}



